.card {
    box-shadow: 0 0 20px rgba(0,0,0,.2);
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    /* max-width: 600px; */
    background: #005E9B;
    flex-basis: 250px;
    color: white;
    padding: 2em;
    text-align: left;
    
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-column-gap: 2em;
    grid-template-areas: 
      "image name"
      "image position"
      "social description";
  }
  
  .profile-name     { grid-area: name; }
  .profile-position { grid-area: position; }
  .profile-info     { grid-area: description; }
  .profile-img      { grid-area: image; }
  .social-list      { grid-area: social; }

li {list-style-type: none;}
.card-btn {
  border-radius: 50%;
  height: 50px;
  width: 50px;
  position: absolute;
  border: none;
  bottom: 10%;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
  font-family: FontAwesome;
  outline: none;
}

.back {
  top: 50%;
  left: 1%;
  background: #43a047;
}

.back::after {
  content: "\F0A8";
}

.next {
  top: 50%;
  right: 1%;
  background: #43a047;
}

.next::after {
  content: "\F0A9";
}

.next:hover::after,
.back:hover::after {
  -webkit-animation: iconBounce 0.57s 1;
          animation: iconBounce 0.57s 1;
}

@-webkit-keyframes iconBounce {
  0% {
    font-size: 24px;
  }

  50% {
    font-size: 28px;
  }

  100% {
    font-size: 24px;
  }
}

@keyframes iconBounce {
  0% {
    font-size: 24px;
  }

  50% {
    font-size: 28px;
  }

  100% {
    font-size: 24px;
  }
}

